// ==========================================

function TasksTab() {
  const [tasks, setTasks] = useState<Task[]>(mockTasks)
  const [viewMode, setViewMode] = useState<'kanban' | 'list'>('kanban')
  const [searchQuery, setSearchQuery] = useState('')
  const [projectFilter, setProjectFilter] = useState('all')

  const projects = [...new Set(tasks.map(t => t.projectName))]

  const filteredTasks = tasks.filter(task => {
    const matchesSearch = task.title.toLowerCase().includes(searchQuery.toLowerCase())
    const matchesProject = projectFilter === 'all' || task.projectName === projectFilter
    return matchesSearch && matchesProject
  })

  const tasksByStatus = KANBAN_COLUMNS.reduce((acc, column) => {
    acc[column.id] = filteredTasks
      .filter(task => task.status === column.id)
      .sort((a, b) => a.position - b.position)
    return acc
  }, {} as Record<string, Task[]>)

  const totalTasks = tasks.length
  const completedTasks = tasks.filter(t => t.status === 'completed').length
  const blockedTasks = tasks.filter(t => t.status === 'blocked').length
  const aiGeneratedTasks = tasks.filter(t => t.aiGenerated).length

  const handleDragStart = (e: React.DragEvent, taskId: string) => {
    e.dataTransfer.setData('taskId', taskId)
  }

  const handleDrop = (e: React.DragEvent, newStatus: string) => {
    e.preventDefault()
    const taskId = e.dataTransfer.getData('taskId')
    setTasks(prev => prev.map(task => 
      task.id === taskId ? { ...task, status: newStatus as Task['status'] } : task
    ))
  }

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault()
  }

  return (
    <div className="space-y-6">
      {/* Stats Bar */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <div className="bg-white rounded-xl border border-gray-200 p-4">
          <div className="text-sm text-gray-500">Total Tasks</div>
          <div className="text-2xl font-bold text-gray-900">{totalTasks}</div>
        </div>
        <div className="bg-white rounded-xl border border-green-200 p-4">
          <div className="text-sm text-green-600">Completed</div>
          <div className="text-2xl font-bold text-green-600">{completedTasks}</div>
        </div>
        <div className="bg-white rounded-xl border border-red-200 p-4">
          <div className="text-sm text-red-600">Blocked</div>
          <div className="text-2xl font-bold text-red-600">{blockedTasks}</div>
        </div>
        <div className="bg-white rounded-xl border border-purple-200 p-4">
          <div className="text-sm text-purple-600">AI Generated</div>
          <div className="text-2xl font-bold text-purple-600">{aiGeneratedTasks} ü§ñ</div>
        </div>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-xl border border-gray-200 p-4">
        <div className="flex flex-col md:flex-row md:items-center gap-4">
          <div className="flex-1">
            <div className="relative">
              <i className="fas fa-search absolute left-3 top-1/2 -translate-y-1/2 text-gray-400"></i>
              <input
                type="text"
                placeholder="Search tasks..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-theme-secondary focus:border-transparent"
              />
            </div>
          </div>
          <div className="flex items-center gap-3">
            <select
              value={projectFilter}
              onChange={(e) => setProjectFilter(e.target.value)}
              className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-theme-secondary focus:border-transparent"
            >
              <option value="all">All Projects</option>
              {projects.map(project => (
                <option key={project} value={project}>{project}</option>
              ))}
            </select>
            <div className="flex items-center bg-gray-100 rounded-lg p-1">
              <button
                onClick={() => setViewMode('kanban')}
                className={`px-3 py-1 rounded text-sm font-medium ${viewMode === 'kanban' ? 'bg-white shadow-sm' : ''}`}
              >
                Kanban
              </button>
              <button
                onClick={() => setViewMode('list')}
                className={`px-3 py-1 rounded text-sm font-medium ${viewMode === 'list' ? 'bg-white shadow-sm' : ''}`}
              >
                List
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Kanban Board */}
      {viewMode === 'kanban' ? (
        <div className="flex gap-4 overflow-x-auto pb-4">
          {KANBAN_COLUMNS.map((column) => (
            <div
              key={column.id}
              className={`flex-shrink-0 w-80 ${column.bgColor} rounded-xl border border-gray-200`}
              onDrop={(e) => handleDrop(e, column.id)}
              onDragOver={handleDragOver}
            >
              <div className="px-4 py-3 border-b border-gray-200 flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <div className="w-3 h-3 rounded-full" style={{ backgroundColor: column.color }}></div>
                  <h3 className="font-semibold text-gray-900">{column.name}</h3>
                  <span className="px-2 py-0.5 bg-white text-gray-600 text-xs font-medium rounded-full">
                    {tasksByStatus[column.id]?.length || 0}
                  </span>
                </div>
                {column.limit && (
                  <span className="text-xs text-gray-500">WIP: {column.limit}</span>
                )}
              </div>
              <div className="p-3 space-y-3 min-h-[200px] max-h-[calc(100vh-400px)] overflow-y-auto">
                {tasksByStatus[column.id]?.map((task) => (
                  <div
                    key={task.id}
                    draggable
                    onDragStart={(e) => handleDragStart(e, task.id)}
                    className="bg-white rounded-lg border border-gray-200 p-3 cursor-pointer hover:shadow-md hover:border-theme-secondary transition-all card-theme-hover"
                  >
                    <div className="flex items-start gap-2 mb-2">
                      <span className="text-sm">{TYPE_ICONS[task.type]}</span>
                      <div className="flex-1 min-w-0">
                        <h4 className="font-medium text-gray-900 text-sm line-clamp-2">{task.title}</h4>
                      </div>
                      {task.aiGenerated && <span className="text-xs" title="AI Generated">ü§ñ</span>}
                    </div>
                    <div className="flex items-center gap-1 mb-2 flex-wrap">
                      <span className={`px-1.5 py-0.5 text-xs font-medium rounded ${PRIORITY_COLORS[task.priority]}`}>
                        {task.priority}
                      </span>
                      {task.tags.slice(0, 2).map(tag => (
                        <span key={tag} className="px-1.5 py-0.5 bg-gray-100 text-gray-600 text-xs rounded">{tag}</span>
                      ))}
                    </div>
                    {task.checklistItems > 0 && (
                      <div className="mb-2">
                        <div className="flex items-center gap-2 text-xs text-gray-500 mb-1">
                          <span>‚úì {task.checklistCompleted}/{task.checklistItems}</span>
                        </div>
                        <div className="w-full bg-gray-200 rounded-full h-1">
                          <div 
                            className="bg-theme-success h-1 rounded-full"
                            style={{ width: `${(task.checklistCompleted / task.checklistItems) * 100}%` }}
                          ></div>
                        </div>
                      </div>
                    )}
                    <div className="flex items-center justify-between text-xs text-gray-500">
                      <div className="flex items-center gap-2">
                        {task.assignee ? (
                          <div 
                            className="w-6 h-6 rounded-full bg-gray-300 flex items-center justify-center text-xs font-medium"
                            title={task.assignee.name}
                          >
                            {task.assignee.name.charAt(0)}
                          </div>
                        ) : (
                          <span className="text-gray-400">Unassigned</span>
                        )}
                      </div>
                      <div className="flex items-center gap-2">
                        {task.dueDate && (
                          <span className={new Date(task.dueDate) < new Date() && task.status !== 'completed' ? 'text-red-500' : ''}>
                            üìÖ {new Date(task.dueDate).toLocaleDateString('en-US', { month: 'short', day: 'numeric' })}
                          </span>
                        )}
                        {task.commentCount > 0 && <span>üí¨ {task.commentCount}</span>}
                      </div>
                    </div>
                  </div>
                ))}
                {(!tasksByStatus[column.id] || tasksByStatus[column.id].length === 0) && (
                  <div className="text-center py-8 text-gray-400 text-sm">No tasks</div>
                )}
              </div>
              <div className="p-3 border-t border-gray-200">
                <button className="w-full py-2 text-sm text-gray-500 hover:text-theme-secondary hover:bg-white rounded-lg transition-colors flex items-center justify-center gap-2">
                  <i className="fas fa-plus"></i> Add Task
                </button>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="bg-white rounded-xl border border-gray-200 overflow-hidden">
          <table className="w-full">
            <thead className="bg-gray-50 border-b border-gray-200">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Task</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Project</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Priority</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Assignee</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Due Date</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-100">
              {filteredTasks.map((task) => (
                <tr key={task.id} className="hover:bg-gray-50 transition-colors">
                  <td className="px-6 py-4">
                    <div className="flex items-center gap-2">
                      <span>{TYPE_ICONS[task.type]}</span>
                      <div>
                        <div className="font-medium text-gray-900 flex items-center gap-2">
                          {task.title}
                          {task.aiGenerated && <span className="text-xs">ü§ñ</span>}
                        </div>
                        <div className="text-sm text-gray-500 truncate max-w-xs">{task.description}</div>
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4 text-sm text-gray-600">{task.projectName}</td>
                  <td className="px-6 py-4">
                    <span 
                      className={`px-2 py-1 text-xs font-medium rounded-full ${TASK_STATUS_COLORS[task.status].bg} ${TASK_STATUS_COLORS[task.status].text}`}
                    >
                      {task.status.replace('_', ' ')}
                    </span>
                  </td>
                  <td className="px-6 py-4">
                    <span className={`px-2 py-1 text-xs font-medium rounded ${PRIORITY_COLORS[task.priority]}`}>
                      {task.priority}
                    </span>
                  </td>
                  <td className="px-6 py-4">
                    {task.assignee ? (
                      <div className="flex items-center gap-2">
                        <div className="w-6 h-6 rounded-full bg-gray-300 flex items-center justify-center text-xs font-medium">
                          {task.assignee.name.charAt(0)}
                        </div>
                        <span className="text-sm">{task.assignee.name.split(' ')[0]}</span>
                      </div>
                    ) : (
                      <span className="text-gray-400 text-sm">Unassigned</span>
                    )}
                  </td>
                  <td className="px-6 py-4 text-sm">
                    {task.dueDate ? (
                      <span className={new Date(task.dueDate) < new Date() && task.status !== 'completed' ? 'text-red-500' : ''}>
                        {new Date(task.dueDate).toLocaleDateString()}
                      </span>
                    ) : '-'}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  )
}

// ==========================================
// MY TASKS TAB
// ==========================================

function MyTasksTab() {
  const [tasks, setTasks] = useState<Task[]>(mockTasks.filter(t => t.assignee))
  const [filter, setFilter] = useState<'all' | 'today' | 'week' | 'overdue'>('all')
  const [showCompleted, setShowCompleted] = useState(false)

  const today = new Date()
  today.setHours(0, 0, 0, 0)
  const weekFromNow = new Date(today)
  weekFromNow.setDate(weekFromNow.getDate() + 7)

  const filteredTasks = tasks.filter(task => {
    if (task.status === 'completed' && !showCompleted) return false
    
    if (filter === 'today' && task.dueDate) {
      const dueDate = new Date(task.dueDate)
      dueDate.setHours(0, 0, 0, 0)
      return dueDate.getTime() === today.getTime()
    }
    if (filter === 'week' && task.dueDate) {
      const dueDate = new Date(task.dueDate)
      return dueDate >= today && dueDate <= weekFromNow
    }
    if (filter === 'overdue' && task.dueDate) {
      const dueDate = new Date(task.dueDate)
      return dueDate < today && task.status !== 'completed'
    }
    return true
  })

  const tasksByStatus = {
    blocked: filteredTasks.filter(t => t.status === 'blocked'),
    in_progress: filteredTasks.filter(t => t.status === 'in_progress'),
    in_review: filteredTasks.filter(t => t.status === 'in_review'),
    todo: filteredTasks.filter(t => t.status === 'todo'),
    backlog: filteredTasks.filter(t => t.status === 'backlog'),
    completed: filteredTasks.filter(t => t.status === 'completed'),
  }

  const stats = {
    total: tasks.filter(t => t.status !== 'completed').length,
    inProgress: tasks.filter(t => t.status === 'in_progress').length,
    blocked: tasks.filter(t => t.status === 'blocked').length,
    overdue: tasks.filter(t => t.dueDate && new Date(t.dueDate) < today && t.status !== 'completed').length,
    completed: tasks.filter(t => t.status === 'completed').length,
  }

  const toggleTaskComplete = (taskId: string) => {
    setTasks(prev => prev.map(task => 
      task.id === taskId 
        ? { ...task, status: task.status === 'completed' ? 'todo' : 'completed' as const }
        : task
    ))
  }

  return (
    <div className="space-y-6">
      {/* Stats */}
      <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
        <div className="bg-white rounded-xl border border-gray-200 p-4">
          <div className="text-sm text-gray-500">Total Active</div>
          <div className="text-2xl font-bold text-gray-900">{stats.total}</div>
        </div>
        <div className="bg-white rounded-xl border border-yellow-200 p-4">
          <div className="text-sm text-yellow-600">In Progress</div>
          <div className="text-2xl font-bold text-yellow-600">{stats.inProgress}</div>
        </div>
        <div className="bg-white rounded-xl border border-red-200 p-4">
          <div className="text-sm text-red-600">Blocked</div>
          <div className="text-2xl font-bold text-red-600">{stats.blocked}</div>
        </div>
        <div className="bg-white rounded-xl border border-orange-200 p-4">
          <div className="text-sm text-orange-600">Overdue</div>
          <div className="text-2xl font-bold text-orange-600">{stats.overdue}</div>
        </div>
        <div className="bg-white rounded-xl border border-green-200 p-4">
          <div className="text-sm text-green-600">Completed</div>
          <div className="text-2xl font-bold text-green-600">{stats.completed}</div>
        </div>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-xl border border-gray-200 p-4">
        <div className="flex flex-wrap items-center gap-4">
          <div className="flex items-center gap-2">
            {(['all', 'today', 'week', 'overdue'] as const).map((f) => (
              <button
                key={f}
                onClick={() => setFilter(f)}
                className={`px-3 py-1.5 rounded-lg text-sm font-medium transition-colors ${
                  filter === f 
                    ? f === 'overdue' ? 'bg-red-500 text-white' : 'bg-theme-secondary text-white btn-theme-primary'
                    : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
                }`}
              >
                {f === 'all' ? 'All' : f === 'today' ? 'Today' : f === 'week' ? 'This Week' : 'Overdue'}
              </button>
            ))}
          </div>
          <div className="flex-1"></div>
          <label className="flex items-center gap-2 cursor-pointer">
            <input
              type="checkbox"
              checked={showCompleted}
              onChange={(e) => setShowCompleted(e.target.checked)}
              className="h-4 w-4 rounded border-gray-300 accent-theme-secondary"
            />
            <span className="text-sm text-gray-600">Show Completed</span>
          </label>
        </div>
      </div>

      {/* Task List Grouped by Status */}
      <div className="space-y-6">
        {tasksByStatus.blocked.length > 0 && (
          <TaskSection title="üöß Blocked" tasks={tasksByStatus.blocked} onToggleComplete={toggleTaskComplete} bgColor="bg-red-50" borderColor="border-red-200" />
        )}
        {tasksByStatus.in_progress.length > 0 && (
          <TaskSection title="üîÑ In Progress" tasks={tasksByStatus.in_progress} onToggleComplete={toggleTaskComplete} bgColor="bg-yellow-50" borderColor="border-yellow-200" />
        )}
        {tasksByStatus.in_review.length > 0 && (
          <TaskSection title="üëÄ In Review" tasks={tasksByStatus.in_review} onToggleComplete={toggleTaskComplete} bgColor="bg-purple-50" borderColor="border-purple-200" />
        )}
        {tasksByStatus.todo.length > 0 && (
          <TaskSection title="üìã To Do" tasks={tasksByStatus.todo} onToggleComplete={toggleTaskComplete} bgColor="bg-blue-50" borderColor="border-blue-200" />
        )}
        {tasksByStatus.backlog.length > 0 && (
          <TaskSection title="üì¶ Backlog" tasks={tasksByStatus.backlog} onToggleComplete={toggleTaskComplete} bgColor="bg-gray-50" borderColor="border-gray-200" />
        )}
        {showCompleted && tasksByStatus.completed.length > 0 && (
          <TaskSection title="‚úÖ Completed" tasks={tasksByStatus.completed} onToggleComplete={toggleTaskComplete} bgColor="bg-green-50" borderColor="border-green-200" />
        )}
      </div>

      {filteredTasks.length === 0 && (
        <div className="text-center py-12 bg-white rounded-xl border border-gray-200">
          <i className="fas fa-check-circle text-5xl text-gray-300 mb-4"></i>
          <h3 className="text-lg font-medium text-gray-900">No tasks found</h3>
          <p className="text-gray-500 mt-1">{filter === 'overdue' ? 'Great job! No overdue tasks.' : 'Try adjusting your filters.'}</p>
        </div>
      )}
    </div>
  )
}

function TaskSection({ title, tasks, onToggleComplete, bgColor, borderColor }: { 
  title: string
  tasks: Task[]
  onToggleComplete: (id: string) => void
  bgColor: string
  borderColor: string
}) {
  const today = new Date()
  today.setHours(0, 0, 0, 0)

  return (
    <div className={`rounded-xl ${bgColor} border ${borderColor} overflow-hidden`}>
      <div className="px-4 py-3 border-b border-gray-200/50 flex items-center justify-between">
        <h2 className="font-semibold text-gray-900">{title}</h2>
        <span className="text-sm text-gray-500">{tasks.length} tasks</span>
      </div>
      <div className="divide-y divide-gray-200/50">
        {tasks.map((task) => {
          const isOverdue = task.dueDate && new Date(task.dueDate) < today && task.status !== 'completed'
          
          return (
            <div key={task.id} className="px-4 py-3 bg-white/60 hover:bg-white transition-colors flex items-start gap-3">
              <input
                type="checkbox"
                checked={task.status === 'completed'}
                onChange={() => onToggleComplete(task.id)}
                className="mt-1 h-4 w-4 rounded border-gray-300 accent-theme-secondary"
              />
              <div className="flex-1 min-w-0">
                <div className="flex items-center gap-2 flex-wrap">
                  <span className={`font-medium ${task.status === 'completed' ? 'line-through text-gray-400' : 'text-gray-900'}`}>
                    {task.title}
                  </span>
                  {task.aiGenerated && <span className="text-xs" title="AI Generated">ü§ñ</span>}
                  <span className={`px-1.5 py-0.5 text-xs font-medium rounded ${PRIORITY_COLORS[task.priority]}`}>
                    {task.priority}
                  </span>
                </div>
                <div className="flex items-center gap-3 mt-1 text-sm">
                  <span className="flex items-center gap-1" style={{ color: task.projectColor }}>
                    <span className="w-2 h-2 rounded-full" style={{ backgroundColor: task.projectColor }}></span>
                    {task.projectName}
                  </span>
                  {task.dueDate && (
                    <span className={isOverdue ? 'text-red-500 font-medium' : 'text-gray-500'}>
                      üìÖ {isOverdue ? 'Overdue: ' : ''}{new Date(task.dueDate).toLocaleDateString('en-US', { month: 'short', day: 'numeric' })}
                    </span>
                  )}
                  {task.checklistItems > 0 && (
                    <span className="text-gray-500">‚úì {task.checklistCompleted}/{task.checklistItems}</span>
                  )}
                </div>
              </div>
              <div className="text-right text-sm">
                <div className="text-gray-600">{task.trackedHours}h</div>
                <div className="text-xs text-gray-400">/ {task.estimatedHours}h</div>
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

// ==========================================
// REPORTS TAB
// ==========================================

function ReportsTab() {
  const [projects] = useState(mockProjects)
  const [activeReportTab, setActiveReportTab] = useState<'overview' | 'team' | 'ai-insights'>('overview')

  const totals = {
    projects: projects.length,
    tasks: projects.reduce((sum, p) => sum + p.totalTasks, 0),
    completedTasks: projects.reduce((sum, p) => sum + p.completedTasks, 0),
    trackedHours: projects.reduce((sum, p) => sum + p.trackedHours, 0),
    budget: projects.reduce((sum, p) => sum + (p.budgetAmount || 0), 0),
    spent: projects.reduce((sum, p) => sum + p.trackedAmount, 0),
  }

  const completionRate = Math.round((totals.completedTasks / totals.tasks) * 100)
  const budgetUsage = Math.round((totals.spent / totals.budget) * 100)
  const avgProgress = Math.round(projects.reduce((sum, p) => sum + p.progress, 0) / projects.length)

  return (
    <div className="space-y-6">
      {/* Report Tabs */}
      <div className="border-b border-gray-200">
        <nav className="flex gap-8">
          {[
            { id: 'overview', label: 'Overview', icon: 'üìà' },
            { id: 'team', label: 'Team Performance', icon: 'üë•' },
            { id: 'ai-insights', label: 'AI Insights', icon: 'ü§ñ' },
          ].map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveReportTab(tab.id as typeof activeReportTab)}
              className={`pb-3 px-1 text-sm font-medium border-b-2 transition-colors flex items-center gap-2 ${
                activeReportTab === tab.id
                  ? 'border-theme-secondary text-theme-secondary'
                  : 'border-transparent text-gray-500 hover:text-gray-700'
              }`}
            >
              <span>{tab.icon}</span>
              {tab.label}
            </button>
          ))}
        </nav>
      </div>

      {activeReportTab === 'overview' && (
        <div className="space-y-6">
          {/* Key Metrics */}
          <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-4">
            <div className="bg-white rounded-xl border border-gray-200 p-4">
              <div className="text-sm text-gray-500">Total Tasks</div>
              <div className="text-2xl font-bold text-gray-900">{totals.tasks}</div>
              <div className="text-xs text-theme-success mt-1">+12 this week</div>
            </div>
            <div className="bg-white rounded-xl border border-gray-200 p-4">
              <div className="text-sm text-gray-500">Completion Rate</div>
              <div className="text-2xl font-bold text-theme-success">{completionRate}%</div>
              <div className="w-full bg-gray-200 rounded-full h-1.5 mt-2">
                <div className="bg-theme-success h-1.5 rounded-full" style={{ width: `${completionRate}%` }}></div>
              </div>
            </div>
            <div className="bg-white rounded-xl border border-gray-200 p-4">
              <div className="text-sm text-gray-500">Hours Tracked</div>
              <div className="text-2xl font-bold text-gray-900">{totals.trackedHours}h</div>
            </div>
            <div className="bg-white rounded-xl border border-gray-200 p-4">
              <div className="text-sm text-gray-500">Budget Used</div>
              <div className="text-2xl font-bold text-blue-600">{budgetUsage}%</div>
              <div className="text-xs text-gray-500 mt-1">${(totals.spent / 1000).toFixed(0)}k / ${(totals.budget / 1000).toFixed(0)}k</div>
            </div>
            <div className="bg-white rounded-xl border border-gray-200 p-4">
              <div className="text-sm text-gray-500">Avg Progress</div>
              <div className="text-2xl font-bold text-indigo-600">{avgProgress}%</div>
              <div className="w-full bg-gray-200 rounded-full h-1.5 mt-2">
                <div className="bg-indigo-600 h-1.5 rounded-full" style={{ width: `${avgProgress}%` }}></div>
              </div>
            </div>
            <div className="bg-white rounded-xl border border-gray-200 p-4">
              <div className="text-sm text-gray-500">AI Alerts</div>
              <div className="text-2xl font-bold text-orange-600">{mockAIAlerts.length}</div>
              <div className="text-xs text-orange-500 mt-1">actionable insights</div>
            </div>
          </div>

          {/* Project Summary Table */}
          <div className="bg-white rounded-xl border border-gray-200 overflow-hidden">
            <div className="px-6 py-4 border-b border-gray-200">
              <h3 className="font-semibold text-gray-900">Project Summary</h3>
            </div>
            <table className="w-full">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Project</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Status</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Progress</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Tasks</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Hours</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Budget</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-100">
                {projects.map((project) => (
                  <tr key={project.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4">
                      <div className="flex items-center gap-3">
                        <div className="w-3 h-3 rounded-full" style={{ backgroundColor: project.color }}></div>
                        <span className="font-medium text-gray-900">{project.name}</span>
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <span className={`px-2 py-1 text-xs font-medium rounded-full ${STATUS_COLORS[project.status]}`}>
                        {project.status}
                      </span>
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex items-center gap-2">
                        <div className="w-24 bg-gray-200 rounded-full h-2">
                          <div className="h-2 rounded-full" style={{ width: `${project.progress}%`, backgroundColor: project.color }}></div>
                        </div>
                        <span className="text-sm font-medium">{project.progress}%</span>
                      </div>
                    </td>
                    <td className="px-6 py-4 text-sm">
                      <span className="text-theme-success">{project.completedTasks}</span>
                      <span className="text-gray-400"> / {project.totalTasks}</span>
                    </td>
                    <td className="px-6 py-4 text-sm">{project.trackedHours}h</td>
                    <td className="px-6 py-4 text-sm">
                      ${(project.trackedAmount / 1000).toFixed(0)}k
                      {project.budgetAmount && <span className="text-gray-400"> / ${(project.budgetAmount / 1000).toFixed(0)}k</span>}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {activeReportTab === 'ai-insights' && (
        <div className="space-y-6">
          {/* AI Summary Banner */}
          <div className="bg-gradient-to-r from-purple-600 to-indigo-600 rounded-xl p-6 text-white">
            <div className="flex items-center gap-4">
              <div className="w-14 h-14 bg-white/20 rounded-xl flex items-center justify-center text-3xl">ü§ñ</div>
              <div>
                <h2 className="text-xl font-bold">AI Analysis Summary</h2>
                <p className="text-purple-100 mt-1">
                  Based on analysis of {totals.tasks} tasks across {totals.projects} projects, 
                  I&apos;ve identified {mockAIAlerts.length} actionable improvements.
                </p>
              </div>
            </div>
          </div>

          {/* Insights List */}
          <div className="space-y-4">
            {mockAIAlerts.map((alert) => (
              <div key={alert.id} className={`rounded-xl border p-5 ${SEVERITY_COLORS[alert.severity]}`}>
                <div className="flex items-start gap-4">
                  <span className="text-2xl">{ALERT_ICONS[alert.type]}</span>
                  <div className="flex-1">
                    <div className="flex items-center gap-2 mb-1">
                      <h3 className="font-semibold">{alert.title}</h3>
                      <span className={`px-2 py-0.5 text-xs font-medium rounded-full ${
                        alert.severity === 'critical' ? 'bg-red-100 text-red-700' :
                        alert.severity === 'warning' ? 'bg-yellow-100 text-yellow-700' : 'bg-blue-100 text-blue-700'
                      }`}>
                        {alert.severity}
                      </span>
                    </div>
                    <p className="text-sm">{alert.description}</p>
                    <div className="mt-3 flex items-center gap-2 text-sm">
                      <span className="font-medium">üí° Suggested Action:</span>
                      <span>{alert.suggestedAction}</span>
                    </div>
                  </div>
                  <button className="px-4 py-2 bg-white border border-gray-300 rounded-lg text-sm font-medium hover:bg-gray-50 transition-colors">
                    Take Action
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {activeReportTab === 'team' && (
        <div className="text-center py-12 bg-white rounded-xl border border-gray-200">
          <i className="fas fa-users text-5xl text-gray-300 mb-4"></i>
          <h3 className="text-lg font-medium text-gray-900">Team Performance</h3>
          <p className="text-gray-500 mt-1">Team analytics coming soon</p>
        </div>
      )}
    </div>
  )
}

